*{
  margin: 0;
  padding: 0;
  
  font-family: Metropolis, -apple-system, "system-ui", "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: white;
}

body {
  background-color:black;
}


header {
  align-items: center;
  display: flex;
  height: 120px;
  justify-content: space-between;
  position: fixed;
  top:0;
  z-index:10;
  width:100%;
  
  background-color:black;
  border-bottom:1px solid #333333;
}

header h2 {
  margin-left: 100px;
}


.header-right {
  line-height:40px;
  margin-right: 100px;
  padding:0rem 1.2rem;
  max-width: 7rem;

  background-color: #F24F83;
  border-radius: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.header-right span {
  margin-right: 0.5rem;
}

.main-wrapper {
  display: flex;
  flex-direction: column;
  margin:150px 100px;
  row-gap: 2rem;
}

.main-wrapper > h3 {
  font-size: 24px;
  font-weight: bold;
}

.search-wrapper {
  align-items: center; 
  display: flex; 
  flex-flow:wrap;
  justify-content: space-between; 
}

.search-input-block {
  padding: 15px;
  width: 30%;

  border-radius: 1rem; 
  border: 1.5px solid rgb(46, 46, 46); 
}

.search-input-block input {
  width: 90%;

  font-size: 1rem;
  
  background-color: black;
  border:1px solid black;
}

.search-sort {
  align-items: center;
  display: flex; 
  column-gap: 0.5rem;
}

.search-sort p {
  font-size: 16px;
}

.search-sort > div {
  display: flex; 
  column-gap: 0.5rem;
}

.search-sort button {
  padding: 15px;

  font-size: 16px;

  background-color: #161616;
  border:1px solid black;
  border-radius: 12px;
  color: #B3B3B3;
}


.bond-wrapper {
  display: flex;
  flex-flow:wrap;
  gap: 2rem;
  justify-content: space-between;
}

.bond {
  padding: 24px;
  width:38%;
  
  background-color: #1A1A1A;
  border:1px solid #333333;
  border-radius: 40px;

  cursor:pointer;
}

.bond-upper-half {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  margin-bottom: 1rem;
}

.bond-upper-half-upper {
  align-items: center;
  display: flex;
  flex-flow:wrap;
  justify-content: space-between;
}

.bond-upper-half-upper h2 {
  line-height: 110%;

  font-size:35px;
  font-weight:bold;

  background: linear-gradient(to right, rgb(0, 240, 221), rgb(220, 31, 255));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.bond-upper-half-upper p {
  color:#B3B3B3;
}

.bond-upper-half-upper span {
  margin: 0px 5px 0px 5px;
  padding: 5px;

  font-weight: bold;

  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  color:#FFFFFF;
}

.bond-upper-half-bottom {
  display: flex;
  column-gap: 1rem;
}

.bond-upper-half-bottom {
  flex-grow: 1;
}


.bond-button {
  padding: 6px 12px 6px 12px;

  font-size:14px;

  background: #333333; 
  border: 1.5px solid rgb(46, 46, 46);
  border-radius: 1rem;
  color:#B3B3B3;
}

.bond-bottom-half {
  column-gap: 1rem;
  display: flex;
  margin-top: 1rem;
}

.bond-bottom-half  > div {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}

.bond-bottom-half h6 {
  font-size: 14px; 

  color: #B3B3B3;
}

.price, .remain-amount {
  font-size: 30px; 
  font-weight: bold;
}

.price-APR-block {
  align-items: center;
  column-gap: 1rem;
  display: flex;
  flex-flow:wrap;
}

.price-APR-block > button {
  font-size: 16px; 
}

.apr-positive {
  background-color: rgb(3, 38, 21); 
  border: 1.5px solid rgb(6, 97, 53);
  color: rgb(0, 231, 122);
}

.apr-negative {
  background-color: rgb(63, 10, 10); 
  border: 1.5px solid rgb(176, 77, 77);
  color: rgb(255, 105, 105);
}

.bond-bottom-half > div > p {
  font-size: 14px; 
}

.unlock-bond-title {
  margin-top:5rem;
}

.footer {
  display: flex;
  margin-bottom: 4rem;
  margin-left: 100px;
  margin-right: 100px;
  padding-top: 3rem;

  border-top:1px solid #333333;
}

.footer-left{
  flex-grow: 0;
  max-width: 25%;
}

.footer-left a {
  font-size: 20px;
  font-weight: bold;
}

.footer-left > p {
  margin-top: 1rem;

  font-size: 14px;

  color: #B3B3B3;
}

.footer-right {
  column-gap: 4rem;
  display:flex;
  flex-grow: 1;
  justify-content: flex-end;
  margin-right: 6rem;
  width: 720px;
}

.footer-right dev {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  width: 30%;
}

.footer-right span {
  display: block;
  padding-top: 5px;
  padding-bottom: 16px;

  font-weight: bold;
}

.footer-right a {
  display: block;
  padding-bottom: 12px;

  font-size: 14px;

  color: #B3B3B3;
}

/*モーダル部分*/

.bond-modal-wrapper {
  align-items: center;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  padding: 3rem;
  position: absolute;
  z-index: 100;

  background-color: rgba(0, 0, 0, 0.6);
}

.bond-modal {
  height: 80vh;
  padding: 2rem;

  background-color: #1b1b1b;
  border-radius: 20px;
}

.bond-modal > div {
  display: flex; 
  flex-direction: column; 
  justify-content: space-between;
}

.bond-modal-header {
  margin-bottom: 1rem;
}

.bond-modal-header-left {
  display: flex;
  justify-content: space-between; 
}

.bond-modal-header-left >div {
  align-items: center;
  display: flex;
  gap: 1rem;
}

.bond-modal-header-left-title {
  align-items: center;
  display: flex; 
  gap: 1rem;
}

.bond-modal-header-left h2 {
  background: linear-gradient(to right, rgb(0, 240, 221), rgb(220, 31, 255));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  font-size: 35px;
  font-weight: bold;
}

.bond-modal-header-left p {
  color: #B3B3B3;
}

.bond-modal-close {
  margin-right: 1rem;
}

.bond-modal-main {
  display: flex;
  gap: 4rem;
}
.bond-modal-main-left {
  flex: 3;
}

.bond-modal-main-left > div {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.modal-bond-price-available-block {
  gap: 3rem;
  padding: 2rem;

  border: 1.5px solid rgb(46, 46, 46);
  border-radius: 2rem;
}

.project-name {
  margin-bottom: 1rem;
  margin-top: 1rem;
  text-align: left; 

  font-size: 20px; 
}

.project-explain p {
  color: #B3B3B3;
}

.bond-modal-main-right {
  flex: 2;

  background-color: rgb(35, 35, 35); border: 1px solid rgb(46, 46, 46);
  border: 1.5px solid rgb(46, 46, 46);
  border-radius: 1.5rem;
}

.bond-modal-main-right > div {
  padding: 1.5rem;
}

.you-pay-get-block {
  display: flex; 
  flex-direction: column; 
  justify-content: space-between; 
  align-items: center; 
  width: 100%; 
  gap: 0.5rem;
}

.you-pay-get-block > div {
  display: flex; 
  flex-direction: row; 
  justify-content: space-between; 
  align-items: center; 
  width: 100%;
  gap: 1rem;
}
.you-pay-get {
  text-align: left;

  font-size: 14px;

  color: #B3B3B3;
}

.you-pay-balance {
  font-size: 12px;

  color: #B3B3B3;
}

.swap-arrow-block {
  align-items: center;
  display:flex;
  margin-top: 1rem; 
  margin-bottom: 1rem;
}

.swap-arrow-block hr {
  height:0.5px; 
  width: 45%
}

.swap-arrow-block i {
  height: 20%;
  padding: 4px;
  text-align: center;
  width: 10%; 

  border: 1px solid #333333;
  border-radius: 50%;
}

.bond-modal-main-right {
  margin-top: 1rem;
}


.bond-modal-token-price {
  align-items: center;
  display: flex;
  justify-content: space-between; 
}

.bond-modal-token-price-right {
  font-size: 20px;
}
.amount-input-box {
  align-items: center;
  display: flex;
  justify-content: space-between; 
  padding: 0px 8px;

  background-color:#1C1C1C;
  border: 1.5px solid rgb(46, 46, 46);
  border-radius: 1rem;
}

.amount-input-box input {
  padding: 18.5px 0px 18.5px 14px;

  font-size: 16px;
  
  background: none;
  border: 0px none;
  -webkit-tap-highlight-color: transparent;
}

.amount-input-box > div { 
  align-items: center;
  font-size: 13px;
  margin-right: 8px;
}

.amount-input-box button {
  align-items: center;
  padding: 6px 15px;
  text-align: center; 
  
  background-color: rgba(255, 255, 255, 0.12);
  border: 0px none;
  border-radius: 8px;
  box-shadow: none;
}
.amount-input-box p {
  color: rgba(255, 255, 255, 0.3);
}

.amount-input-box-warn {
  margin-left: 14px;
  margin-right: 14px;
  margin-top: 3px;

  font-size: 11px;

  color: #FF6969;
}

.modal-buy-block {
  align-items: center;
  display: flex;
  margin-top: 1.5rem;
}

.modal-buy-block > div {
  flex-grow: 1;
}

.modal-buy-button {
  align-items: center;
  display: inline-flex;
  justify-content: center;
  padding: 10px 16px;
  width: 100%;

  font-size: 16px;

  background-color: #F24F83;
  border: none;
  border-radius: 12px;
}

.modal-buy-button span {
  margin-right: 0.5rem;
}

.slippage-tolerance {
  align-items: center;
  padding: 10px 16px;

  background-color: transparent;
  border: none;
}